import React, { Component } from "react";
import Slider from "react-slick";

class SimpleSlider extends Component {
  render() {
    const settings = {
      dots: false,
      infinite: true,
      speed: 500,
      slidesToShow: 1,
      slidesToScroll: 1
    };

    const { images } = this.props;
    console.log(images, "in slider");
    return (
      <div>
        <h2> Single Item</h2>
        <Slider {...settings}>
          <div
            style={{
              width: "300px",
              height: "300px",
              background: `url(${images[0]})`
            }}
          />
        </Slider>
      </div>
    );
  }
}

export default SimpleSlider;
